import uCharts from '@/components/u-charts/u-charts.js';
import {
	isJSON
} from '@/common/checker.js';
var _self;
var canvaLineA = null;
var lastMoveTime = null; //最后执行移动的时间戳
export default {
	data() {
		return {
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			textarea: '',
			Interactive: '', //交互显示的数据
		}
	},
	onLoad() {
		_self = this;









		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
		this.getServerData();
	},
	methods: {
		getServerData() {
			uni.request({
				url: 'http://api.s/api/app/avgTemp7day',
				data: {},
				success: function(res) {
					console.log(res.data)
					let LineA = {
						categories: [],
						series: []
					};
					LineA.categories = res.data.categories;
					LineA.series = res.data.series;
					_self.textarea = JSON.stringify(res.data);
					_self.showLineA("canvasLineA", LineA);
				},
				fail: () => {
					_self.tips = "网络错误，小程序端请检查合法域名";
				},
			});
		},
		showLineA(canvasId, chartData) {
			canvaLineA = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'line',
				colors: ['#facc14', '#f04864', '#8543e0', '#90ed7d'],
				fontSize: 11,
				padding: [15, 15, 0, 15],
				legend: {
					show: true,
					padding: 5,
					lineHeight: 11,
					margin: 0,
				},
				dataLabel: false,
				dataPointShape: true,
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				xAxis: {
					type: 'grid',
					gridColor: '#CCCCCC',
					gridType: 'dash',
					dashLength: 8,
					//disableGrid:true
				},
				yAxis: {
					gridType: 'dash',
					gridColor: '#CCCCCC',
					dashLength: 8,
				},
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				extra: {
					line: {
						type: 'straight'
					}
				}
			});

		},
		touchLineA(e) {
			lastMoveTime = Date.now();
		},
		moveLineA(e) {
			let currMoveTime = Date.now();
			let duration = currMoveTime - lastMoveTime;
			if (duration < Math.floor(1000 / 60)) return; //每秒60帧
			lastMoveTime = currMoveTime;

			let currIndex = canvaLineA.getCurrentDataIndex(e);
			if (currIndex > -1 && currIndex < canvaLineA.opts.categories.length) {
				let riqi = canvaLineA.opts.categories[currIndex];
				let leibie = canvaLineA.opts.series[0].name;
				let shuju = canvaLineA.opts.series[0].data[currIndex];
				this.Interactive = leibie + ':' + riqi + '-' + shuju + '元';
			}
			canvaLineA.showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});
		},
		touchEndLineA(e) {
			let currIndex = canvaLineA.getCurrentDataIndex(e);
			if (currIndex > -1 && currIndex < canvaLineA.opts.categories.length) {
				let riqi = canvaLineA.opts.categories[currIndex];
				let leibie = canvaLineA.opts.series[0].name;
				let shuju = canvaLineA.opts.series[0].data[currIndex];
				this.Interactive = leibie + ':' + riqi + '-' + shuju + '元';
			}
			canvaLineA.touchLegend(e);
			canvaLineA.showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});
		},
		changeData() {
			if (isJSON(_self.textarea)) {
				let newdata = JSON.parse(_self.textarea);
				canvaLineA.updateData({
					series: newdata.series,
					categories: newdata.categories
				});
			} else {
				uni.showToast({
					title: '数据格式错误',
					image: '../../../static/images/alert-warning.png'
				})
			}
		}
	}
}
